
import Swiper from 'swiper/bundle';

import 'swiper/swiper-bundle.css';

const swiper = new Swiper('.swiper-container', {

    pagination: {
        el: '.swiper-pagination',
        dynamicBullets: true,
    },
    breakpoints: {

        688:{
            slidesPerView:2,
            spaceBetween:30,

        },
        992:{
            slidesPerView:3,
            spaceBetween:30,
        },
        1392:{
            slidesPerView:4,

        }
    }

});

